	module.exports = {"data":{"markdownRemark":{"html":"<h3>Mutual Disclosure of All Reports</h3>\n<p>All reports, including those marked as <i>Not Applicable</i>, <i>Duplicate</i>, and <i>Spam</i> can now be publicly disclosed when both the hacker and the program agree to disclose the report. </p>\n<h3>Request Mediation for Hackers</h3>\n<p>Hackers can now request mediation when they get into a disagreement with a program’s security team.</p>\n<h3>Filter Directory by Programs Offering Bounties</h3>\n<p>Users can now filter the directory by programs offering bounties. Type <code>bounties:yes</code> into the search bar to only view the bounty programs in the directory. </p>\n<h3>Threading for Notification Emails</h3>\n<p>We now support message threading for notification emails so that similar emails are grouped together. </p>\n<h3>Award Bonus</h3>\n<p>We introduce the ability for programs to award a structured bonus in addition to the standard bounty for a vulnerability. Read about it in our <a href=\"https://hackerone.com/blog/uber-launches-first-of-its-kind-hacker-loyalty-program-with-hackerone-bonuses\">blog</a>. </p>\n<h3>Improved Rate Limiter &#x26; Signal Requirements</h3>\n<p>We give programs the ability to tune the Rate Limiter by specifying minimum Signal Requirements for hacker participation. We’ve also updated the Rate Limiter to incorporate additional intelligent inputs. </p>\n<h3>Hacker Invitations by Priority</h3>\n<p>We’ve overhauled the hacker invitation process so that hackers with the highest Reputation, Signal, and Impact will have a greater likelihood of being invited to private programs. Read our <a href=\"https://www.hackerone.com/blog/fair-and-transparent-hacker-invitations\">blog post</a> to learn more about how invitations work. </p>\n<h3>Inline Image Attachments</h3>\n<p>We enable programs and hackers to now add inline image attachments to reports and comments. </p>","frontmatter":{"path":"/changelog/2016/march","title":"March 2016"}}},"pathContext":{}}

